* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}


main{
  width: 1140px;
  margin : auto;
  min-height: 500px;
}


/***************************************************/
/* 게시글 메인화면 */




/* header */
header{
  height: 200px;
  display: flex;
  flex-direction: row;
}


header > section:nth-child(1){ width: 15%;}
header > section:nth-child(2){ width: 70%;}
header > section:nth-child(3){ width: 15%; }




/* 로고 */
header > section:first-child{
  display: flex;
  justify-content: center;
  align-items: center;
 
}


#homeLogo{
  width: 120px;
  height: auto; /* 이미지 기본 비율에 맞게 자동 지정 */
}


/***************************************************/
/* Board Nav */


/* nav */
nav{
  height: 50px;
  position: sticky;
  /* 화면 밖으로 벗어났을 때
  지정된 위치에 고정*/
 
  top : 0; /* 최상단에 붙임 */
 
  background-color: white;
  border-bottom: 2px solid black;
  z-index: 1000;
}


nav > ul{
  display: flex;
 
  /* li 태그 앞에 기호 삭제 */
  list-style: none;
  height: 100%;
}




nav li{
  flex : 0 1 150px;
  /* grow shrink basis */
}


nav a{
  text-decoration: none;
  color : black;
  font-size: 18px;
  font-weight: bold;
  padding : 11px 0;
  display: block;
  text-align: center;
 
  border-radius: 5px;
  transition-duration: 0.1s;
  height: 100%;
}




nav a:hover{
  color : white;
  background-color: #455ba8;
}




/***************************************************/
/* 메인 - 로그인화면 */


/* content */
.main.content{
  height: 800px;
  display: flex;
 
  /* flex 지정 시 기본값 */
  flex-direction: row;
  justify-content: flex-start;
  /* flex-start : flex요소에만 사용 가능한 정렬 방식 */
  /* start : block요소, flex, grid 등에서도 적용 가능한 정렬 방식 */
  align-items: stretch;
}


.main .content-1{
  flex-basis: 70%;
  padding : 20px;
}
.main .content-2{ flex-basis: 30%;}


.main .content-1 > *{
  margin-bottom: 20px;
}


.main .content-1 > pre{
  font-size: 1rem;
  font-family: '고딕';
}


/* 로그인 영역 */
.main .content-2 > div{
  height: 150px;
  padding : 10px;
  margin-top : 10px;


  display: flex;
  flex-direction: column;
  justify-content: center;
}


.main .id-pw-area{
  border: 1px solid #ccc;
  display: flex;


  /* 중심축 방향 크기 지정 */
  flex-basis: 60%;
}


/* id/pw 입력 영역*/
.main .id-pw-area > section:first-child{
  width: 75%;
  display: flex;
  flex-direction: column;
}


.main .id-pw-area > section:first-child > input{
  border : 0;
  border-right : 1px solid #ccc;
  flex-basis: 50%;
  outline: none;
  padding: 5px;
}


/* id input 태그 */
.main input[name='inputId']{
  border-bottom : 1px solid #ccc !important;
}


/* id/pw에 초점이 맞춰졌을 때 */
.main .id-pw-area > section:first-child > input:focus{
  border: 2px solid #455ba8 !important;
}




/* 로그인 버튼 영역 */
.main .id-pw-area > section:last-child{
  width: 25%;
  display: flex;
  justify-content: center;
}


/* 로그인 버튼 */
.main .id-pw-area > section:last-child > button{
  width: 100%;
  background-color: white;
  cursor: pointer;
  border : none;
}


/* 로그인 버튼에 마우스가 올라갔을 때 */
.main .id-pw-area > section:last-child > button:hover{
  background-color: #455ba8;
  color: white;
}




/* 게시판 전체 */
.board-content {
  width: 100%;
  margin: 0 auto;
  padding: 20px;
}

/* 게시글 ul */
.board-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* 게시글 */
.board-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 14px 10px;
  border-bottom: 1px solid lightgray;
}

/* 게시글 정보 영역 */
.board-list li > div:first-child {
  flex-grow: 1;
}

/* 게시글 제목 */
.board-title {
  font-weight: bold;
  font-size: 16px;
  color: black;
  text-decoration: none;
  margin-right: 10px;
}

/* 좋아요 수 */
.board-title span {
  font-size: 14px;
}

/* 작성자, 날짜 */
.writerDate {
  color: gray;
  font-size: 14px;
  margin-top: 4px;
}

/* 모달 버튼 */
#modal {
  background-color: white;
  border: 1px solid darkblue;
  color: darkblue;
  padding: 6px 20px;
  font-size: 14px;
  cursor: pointer;
  
}

#modal:hover {
  background-color: darkblue;
  color: white;
  transition: 0.2s;
}


#board-detail, .modal{
  margin-top: 20px;
  padding: 15px;
  background-color: lightgray;
}

.board-detail-content{
  margin-top: 5px;
  font-size: 12px;
}

/* 에러페이지 */
.error-page{
  display: flex;
  height: 800px;
  justify-content: center;
  align-items: center;
  color: red;
  font-weight: bold;
  font-size: 30px;
  
}

.board-detail-container {
  width: 800px;
  margin: 40px auto;
  padding: 30px;
  border: 1px solid lightgray;
  background-color: #fff;
  font-family: 'Arial', sans-serif;
}

.board-detail-title {
  width: 100%;
  font-size: 24px;
  font-weight: bold;
  border-bottom: 2px solid lightgray;
  padding-bottom: 12px;
  margin-bottom: 20px;
}

.board-detail-meta {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  color: #555;
  margin-bottom: 20px;
  padding-bottom : 10px;
  width: 100%;
  border-bottom: 2px solid lightgray;
}

.board-detail-writer {
  font-weight: 500;
}

.board-detail-dates {
  display: flex;
  gap: 16px;
  text-align: right;
}

.board-detail-dates > div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 40px;
}

.board-detail-dates span {
  min-height: 20px; /* 공백이라도 높이 유지 */
  line-height: 1.4;
}

.board-detail-content2 {
  font-size: 16px;
  line-height: 1.6;
  min-height: 150px;
  margin-bottom: 30px;
  white-space: pre-line;
}

.board-detail-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.board-detail-buttons button {
  padding: 8px 16px;
  font-size: 14px;
  border: none;
  cursor: pointer;
  background-color: darkblue;
  color: white;
}

.board-not-found {
  text-align: center;
  margin-top: 100px;
  font-size: 18px;
  font-weight: bold;
}